import React from 'react';
import styled from 'styled-components';

const MainTitleStyle = styled.h1`
    font-size:20px;
`

export function MainTitle (props) {
    return <MainTitleStyle size={props.InputVariant} color={props.buttonColor} onClick={props.handleClick}>
        {props.children}
    </MainTitleStyle>
}

const SecondTitleStyle = styled.h2`
    font-size:18px;
`

export function SecondTitle (props) {
    return <SecondTitleStyle size={props.InputVariant} color={props.buttonColor} onClick={props.handleClick}>
        {props.children}
    </SecondTitleStyle>
}

const SubTitleStyle = styled.h4`
    font-size:18px;
`

export function SubTitle (props) {
    return <SubTitleStyle size={props.InputVariant} color={props.buttonColor} onClick={props.handleClick}>
        {props.children}
    </SubTitleStyle>
}

const ParagraphStyle = styled.p`
    font-size:18px;
`

export function Paragraph(props) {
    return <ParagraphStyle size={props.InputVariant} color={props.buttonColor} onClick={props.handleClick}>
    {props.children}
    </ParagraphStyle>
}

export function Countinuos(props) {
    return <p size={props.InputVariant} color={props.buttonColor} onClick={props.handleClick}>
        {props.children}
    </p>
}

export function Featured(props) {
    return <input variant={props.InputVariant} color={props.buttonColor} onClick={props.handleClick}>
        {props.children}
    </input>
}

export function Label(props) {
    return <label variant={props.InputVariant} color={props.buttonColor} onClick={props.handleClick}>
        {props.children}
    </label>
}

const LinkStyle = styled.a`
    text-decoration:none;
    color: ${props => props.theme.colors.secondary};
    &:hover{
        color: ${props => props.theme.colors.secondaryHover};
        cursor:pointer;
    }
`

export function Link(props) {
    return <LinkStyle href={props.href} target={props.target}>
        {props.children}
    </LinkStyle>
}

const LinkFooterStyle = styled.a`
    text-decoration:none;
    color: ${props => props.theme.colors.secondary};
    text-decoration:none;
    &:hover{
        color: ${props => props.theme.colors.secondaryHover};
        text-decoration:underline;
        cursor:pointer;
    }
`

export function LinkFooter(props) {
    return <LinkFooterStyle href={props.href} target={props.target}>
        {props.children}
    </LinkFooterStyle>
}