import React from 'react';
import styled from 'styled-components';

const CoverStyle = styled.section`
    width:100%;
    background-color:${props => props.color};
    height:${props => props.height};
    position:relative;
`

export function Cover (props) {
    return <CoverStyle color={props.color} height={props.height}>
        {props.children}
    </CoverStyle>
}

const TitledContainerStyle = styled.section`
    width:100%;
    background-color:${props => props.color};
    padding: 64px ${props => props.theme.containers.desktop.half};
    display:flex;
    flex-direction:column;
    align-items:center;

    & h1, h2{
        margin-bottom:${props => props.theme.margins.full};
    }
`

export function TitledContainer (props) {
    return <TitledContainerStyle color={props.color}>
        {props.children}
    </TitledContainerStyle>
}

const BasicContainerStyle = styled.section`
    width:100%;
    background-color:${props => props.color};
    padding: 64px ${props => props.theme.containers.desktop.half};
    margin-top: ${props => props.marginTop};
    display:flex;
    flex-direction:column;
    align-items:center;

    & h1, h2{
        margin-bottom:20px;
    }
`

export function BasicContainer (props) {
    return <BasicContainerStyle color={props.color} marginTop={props.marginTop}>
        {props.children}
    </BasicContainerStyle>
}
